import React from 'react'
import PropTypes from 'prop-types'

// Styles
import styles from './contact-block.module.scss'

// Components
import Block from '../../components/Block'
import ContactForm from '../../components/ContactForm'

// Icons
import EmailIcon from './email.inline.svg'
import PhoneIcon from './phone.inline.svg'
import LocationIcon from './location.inline.svg'

const ContactBlock = ({ content }) => {
  const {
    city,
    contactFormTitle,
    country,
    email,
    hasContactForm,
    phone,
    streetHouseNumber,
    title,
    zipcode,
  } = content

  const cleanPhone = phone.replace(/-|(0)|\(|\)|\s|/g, '')

  return (
    <Block className={styles.contactBlock} settings={content.settings}>
      <div className={styles.info}>
        <h2 className={styles.title}>{title}</h2>
        <div className={styles.section}>
          <div className={styles.label}>
            <span className={styles.svgContainer}>
              <EmailIcon className={styles.svg} />
            </span>
            <span>Email</span>
          </div>
          <a className={styles.link} href={`mailto:${email}`}>
            {email}
          </a>
        </div>
        <div className={styles.section}>
          <div className={styles.label}>
            <span className={styles.svgContainer}>
              <PhoneIcon className={styles.svg} />
            </span>
            <span>Telefon</span>
          </div>
          <a className={styles.link} href={`tel:${cleanPhone}`}>
            {phone}
          </a>
        </div>
        <div className={styles.section}>
          <div className={styles.label}>
            <span className={styles.svgContainer}>
              <LocationIcon className={styles.svg} />
            </span>
            <span>Adresse</span>
          </div>
          <a className={styles.link} href={'https://google.com'}>
            {streetHouseNumber}
            <br />
            {`${zipcode}, ${city}`}
            <br />
            {country}
          </a>
        </div>
      </div>
      {hasContactForm && (
        <ContactForm extraClasses={styles.form} title={contactFormTitle} />
      )}
    </Block>
  )
}

ContactBlock.propTypes = {
  content: PropTypes.any.isRequired,
}

export default ContactBlock
