import React from 'react'
import PropTypes from 'prop-types'

// Components
import Block from '../../components/Block'

// styles
import styles from './quote-list.module.scss'
import Img from 'gatsby-image'

//svg
import QuoteIcon from './quote.inline.svg'

const QuoteList = ({ content }) => {
  const { settings, quotes } = content
  return (
    <Block className={styles.quoteList} settings={settings}>
      {(content.blockTitle || content.blockText) && (
        <div className={styles.blockHeading}>
          <h2 className={styles.blockTitle}>{content.blockTitle}</h2>
          <p className={styles.blockText}>{content.blockText}</p>
        </div>
      )}
      <div className={styles.quotes}>
        {quotes &&
          quotes.map((person, key) => {
            return (
              <div className={styles.quote} key={key}>
                <div className={styles.upper}>
                  <QuoteIcon className={styles.quoteIcon} />
                  <div className={styles.quoteText}>{person.quote}</div>
                </div>
                <div className={styles.info}>
                  <div className={styles.imageContainer}>
                    <Img
                      className={styles.image}
                      fixed={person.image[0].imageFile.childImageSharp.fixed}
                      alt={person.image[0].alternativeText}
                    />
                  </div>
                  <h3 className={styles.name}>{person.name}</h3>
                  <span className={styles.profession}>{person.profession}</span>
                </div>
              </div>
            )
          })}
      </div>
    </Block>
  )
}

QuoteList.propTypes = {
  content: PropTypes.any.isRequired,
}

export default QuoteList
