import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import ReactMarkdown from 'react-markdown'

// Components
import Block from '../../components/Block'

//Styles
import styles from './text.module.scss'

const Text = ({ content }) => {
  // Text block classes
  const textBlockStyles = classNames({
    [styles.textBlock]: true,
    [content.textAlign]: content.textAlign ? content.textAlign : 'left',
  })

  // Title classes
  const titleClasses = classNames({
    [styles.title]: true,
    [styles[content.titleAlign]]: content.titleAlign
      ? content.titleAlign
      : 'center',
  })

  // Text classes
  const textClasses = classNames({
    [styles.text]: true,
    [styles[content.textAlign]]: content.textAlign ? content.textAlign : 'left',
  })

  return (
    <Block className={textBlockStyles} settings={content.settings}>
      {content.title && <h2 className={titleClasses}>{content.title}</h2>}
      <ReactMarkdown className={textClasses}>{content.text}</ReactMarkdown>
    </Block>
  )
}

Text.propTypes = {
  content: PropTypes.object.isRequired,
}

export default Text
