import React from 'react'
import ReactMarkdown from 'react-markdown'
import Block from '../../components/Block'
import styles from './dealer-location.module.scss'

const DealerLocation = ({ content }) => {
  const Dealers = content.dealers
  return (
    <Block>
      <h1 className={styles.title}>{content.title}</h1>
      <div className={styles.dealers}>
        {Dealers &&
          Dealers.map((dealer, key) => {
            return (
              <a className={styles.dealer} href={dealer.addressLink} key={key}>
                <h3 className={styles.dealerName}>{dealer.name}</h3>
                <div className={styles.dealerAddress}>
                  <ReactMarkdown>{dealer.addressText}</ReactMarkdown>
                </div>
              </a>
            )
          })}
      </div>
    </Block>
  )
}

export default DealerLocation
