import React from 'react'
import PropTypes from 'prop-types'
import ReactMarkdown from 'react-markdown'
import classNames from 'classnames'

// Components
import Block from '../../components/Block'
import Img from 'gatsby-image'

// Styles
import styles from './two-col-layout.module.scss'

// SVGs

const TwoColLayout = ({ content }) => {
  const { settings, media, title, text, video, colReverse } = content

  const imageClasses = classNames({
    [styles.image]: true,
    [styles.png]: 'image/png' === media.mime,
  })

  // get the right Media to show
  const renderMediaTag = () => {
    if (video) {
      //TODO: return video element
      console.log(video)
    } else {
      if ('image/svg+xml' === media.mime) {
        return (
          <img
            className={styles.svg}
            alt={media.alternativeText}
            src={media.imageFile.publicURL}
          />
        )
      }

      console.log(media.mime)

      return (
        <Img
          className={imageClasses}
          alt={media.alternativeText}
          fluid={media.imageFile.childImageSharp.fluid}
        />
      )
    }
  }

  const twoColLayoutClasses = classNames({
    [styles.twoColLayout]: true,
    [styles.reverse]: colReverse,
  })

  return (
    <Block className={twoColLayoutClasses} settings={settings}>
      <div className={styles.col1}>{renderMediaTag()}</div>
      <div className={styles.col2}>
        {title && <h2 className={styles.title}>{title}</h2>}
        <ReactMarkdown>{text}</ReactMarkdown>
      </div>
    </Block>
  )
}

TwoColLayout.propTypes = {
  content: PropTypes.any.isRequired,
}

export default TwoColLayout
