import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { Formik } from 'formik'
import classNames from 'classnames'
import * as Yup from 'Yup'
import axios from 'axios'

// Component
import Input from '../Input'
import Textarea from '../Textarea'

// Styles
import styles from './form.module.scss'

// Loading environments

const ContactForm = ({ title, extraClasses }) => {
  const [formState, setFormState] = useState('')

  const formClasses = classNames({
    [styles.formContainer]: true,
    [extraClasses]: !!extraClasses,
  })

  const contactFormSchema = Yup.object({
    name: Yup.string().required("This field can't be empty!"),
    email: Yup.string()
      .email('Please provide a valid email!')
      .required("This field can't be empty!"),
    subject: Yup.string().required("This field can't be empty!"),
    message: Yup.string().required("This field can't be empty!"),
  })

  return (
    <Formik
      validationSchema={contactFormSchema}
      initialValues={{
        name: '',
        email: '',
        company: '',
        address: '',
        subject: '',
        message: '',
      }}
      onSubmit={values => {
        setFormState('loading')
        const request = async () => {
          const res = await axios({
            method: 'POST',
            url: `${process.env.STRAPI_URL}/contactform`,
            data: values,
          })

          200 === res.status ? setFormState('success') : setFormState('error')
        }

        request()
      }}
    >
      {({
        touched,
        errors,
        values,
        handleChange,
        handleBlur,
        handleSubmit,
      }) => (
        <div className={formClasses}>
          <form className={styles.form} onSubmit={handleSubmit}>
            <legend className={styles.title}>{title}</legend>
            <Input
              touched={touched.name}
              errors={errors.name}
              type={'text'}
              name={'name'}
              value={values.name}
              onChange={handleChange}
              isBlur={handleBlur}
              labelText={'Name'}
              required={true}
            />
            <Input
              touched={touched.email}
              errors={errors.email}
              type={'email'}
              name={'email'}
              value={values.email}
              onChange={handleChange}
              isBlur={handleBlur}
              labelText={'Email'}
              required={true}
            />
            <Input
              type={'text'}
              name={'company'}
              value={values.company}
              onChange={handleChange}
              isBlur={handleBlur}
              labelText={'Unternehmen'}
            />
            <Input
              type={'text'}
              name={'address'}
              value={values.address}
              onChange={handleChange}
              isBlur={handleBlur}
              labelText={'Adresse'}
            />
            <Input
              touched={touched.subject}
              errors={errors.subject}
              extraClasses={styles.subject}
              type={'text'}
              name={'subject'}
              value={values.subject}
              onChange={handleChange}
              isBlur={handleBlur}
              labelText={'Betreff'}
              required={true}
            />
            <Textarea
              isBlur={handleBlur}
              touched={touched.message}
              errors={errors.message}
              extraClasses={styles.message}
              onChange={handleChange}
              value={values.message}
              name={'message'}
              labelText={'Nachricht'}
              required={true}
            />
            {'loading' === formState && (
              <div className={styles.status}>loading...</div>
            )}
            {'success' === formState && (
              <div className={styles.status}>Your email has been sent</div>
            )}
            {'error' === formState && (
              <div className={styles.status}>Oops something went wrong</div>
            )}
            <div className={styles.buttonRow}>
              <button
                disabled={!!formState}
                className={styles.button}
                type={'submit'}
              >
                Senden
              </button>
            </div>
          </form>
        </div>
      )}
    </Formik>
  )
}

ContactForm.propTypes = {
  title: PropTypes.string,
}

ContactForm.defaultProps = {
  title: '',
}

export default ContactForm
