import React from 'react'
import PropTypes from 'prop-types'

//Components
import Text from '../../blocks/Text'
import Hero from '../../blocks/Hero'
import Image from '../../blocks/Image'
import ContactBlock from '../../blocks/ContactBlock'
import QuoteList from '../../blocks/QuoteList'
import TwoColLayout from '../../blocks/TwoColLayout/TwoColLayout'
import DealerLocation from '../../blocks/DealerLocation'

const DynamicZones = ({ pageLayout }) => {
  return pageLayout.map((component, key) => {
    switch (component.__typename) {
      case 'safro_ComponentHeroHero':
        return <Hero key={key} content={component} />
      case 'safro_ComponentTextText':
        return <Text key={key} content={component} />
      case 'safro_ComponentImageImage':
        return <Image key={key} content={component} />
      case 'safro_ComponentContactBlockContactBlock':
        return <ContactBlock key={key} content={component} />
      case 'safro_ComponentQuoteQuote':
        return <QuoteList key={key} content={component} />
      case 'safro_ComponentTwoCalLayoutTwoColLayout':
        return <TwoColLayout key={key} content={component} />
      case 'safro_ComponentDealerLocationDealerLocation':
        return <DealerLocation key={key} content={component} />
      default:
        return null
    }
  })
}

DynamicZones.propTypes = {
  pageLayout: PropTypes.arrayOf(PropTypes.object).isRequired,
}

export default DynamicZones
