import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

// Styles
import styles from './block.module.scss'

const Block = ({ Parent, children, settings, ...props }) => {
  // Spreading our setting options
  const { background, blockId } = settings

  const blockClasses = classNames({
    [styles.block]: true,
    [props.className]: !!props.className,
    [styles.bgNot]: props.noBg && props.noBg,
    [styles[`${background}Bg`]]: !!background && !background.includes('light'),
  })

  return (
    <Parent id={blockId && blockId} className={blockClasses}>
      {children}
    </Parent>
  )
}

Block.propTypes = {
  Parent: PropTypes.string,
  children: PropTypes.any.isRequired,
  props: PropTypes.object,
  settings: PropTypes.object,
}

Block.defaultProps = {
  props: {},
  Parent: 'div',
  settings: {},
}

export default Block
