import React from 'react'
import PropTypes from 'prop-types'
import Img from 'gatsby-image'
import classNames from 'classnames'

// Components
import Block from '../../components/Block'

// Styles
import styles from './hero.module.scss'
import Button from '../../components/Button'

//SVGs
import InnerBg from './inner-bg.inline.svg'
import HeroBorder from '../../images/hero-border.inline.svg'

const Hero = ({ content }) => {
  const heroClasses = classNames({
    [styles.hero]: true,
  })
  return (
    <Block noBg={true} className={heroClasses}>
      <div className={styles.imageContainer}>
        <Img
          fluid={content.heroImage[0].imageFile.childImageSharp.fluid}
          className={styles.image}
          alt={content.heroImage.alternativeText}
        />
      </div>
      {content.title && (
        <div className={styles.inner}>
          <InnerBg className={styles.innerBg} />
          <h1 className={styles.title}>
            {content.subTitle && (
              <small className={styles.subtitle}>{content.subTitle}</small>
            )}
            {content.title}
          </h1>
          {content.buttonText && (
            <Button
              external={content.isExternal}
              extraClasses={styles.heroButton}
              slug={content.buttonUrl}
              buttonText={content.buttonText}
            />
          )}
        </div>
      )}
      <HeroBorder className={styles.heroBorder} />
    </Block>
  )
}

Hero.propTypes = {
  content: PropTypes.object.isRequired,
}

export default Hero
