import React from 'react'
import PropTypes from 'prop-types'

//Components
import Header from '../Header'
import Footer from '../Footer'
import MobileMetaNav from '../MobileMetaNav'
import CallToAction from '../CallToAction'
import Block from '../Block'

// Styles
import styles from './layout.module.scss'
import Button from '../Button'

// Svgs
import HeroBorder from '../../images/hero-border.inline.svg'

const Layout = ({
  children,
  callToAction,
  hasCallToAction,
  hasNoHero,
  globalHeroButton,
  ...props
}) => {
  return (
    <>
      <Header />
      <main>
        {hasNoHero && (
          <Block className={styles.defaultHero} noBg={true}>
            <h1 className={styles.title}>{props.pageTitle}</h1>
            <p className={styles.slogan}>
              Das weltweit erste<span className={styles.special}>Safran</span>
              Getrnk
            </p>
            {globalHeroButton && (
              <Button
                extraClasses={styles.button}
                slug={globalHeroButton.buttonUrl}
                buttonText={globalHeroButton.buttonText}
              />
            )}
            <HeroBorder className={styles.heroBorder} />
          </Block>
        )}
        {children}
        {hasCallToAction && <CallToAction content={callToAction} />}
      </main>
      {/* <MobileMetaNav /> */}
      <Footer />
    </>
  )
}

Layout.propType = {
  children: PropTypes.object.isRequired,
  callToAction: PropTypes.object,
  hasCallToAction: PropTypes.bool,
  hasNoHero: PropTypes.bool,
  globalHeroButton: PropTypes.object,
}

Layout.defaultProps = {
  callToAction: {},
  hasCallToAction: false,
  hasNoHero: true,
  globalHeroButton: {},
}

export default Layout
