import React, { useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

// Styles
import styles from './input.module.scss'

const Input = ({
  touched,
  isBlur,
  errors,
  name,
  type,
  value,
  onChange,
  extraClasses,
  labelExtraClasses,
  labelText,
  required,
}) => {
  const [blur, setBlur] = useState(false)

  const inputClasses = classNames({
    [styles.input]: true,
    [styles.clicked]: blur,
    [styles.filled]: !!errors !== true && !!value,
    [styles.inputError]: !!errors && !!touched,
    [extraClasses]: !!extraClasses,
  })

  const labelClasses = classNames({
    [styles.label]: true,
    [styles.clicked]: blur,
    [styles.filled]: !!errors !== true && !!value,
    [styles.inputError]: !!errors && !!touched,
    [labelExtraClasses]: !!labelExtraClasses,
  })

  return (
    <div className={inputClasses}>
      <label className={labelClasses} htmlFor={name}>
        {`${labelText} ${required ? '*' : ''}`}
      </label>
      <input
        autoComplete="off"
        name={name}
        className={styles.field}
        value={value}
        type={type}
        onChange={onChange}
        onBlur={isBlur}
        onFocus={() => setBlur(true)}
        onBlurCapture={() => setBlur(false)}
      />
      {touched && errors && <div className={styles.error}>{errors}</div>}
    </div>
  )
}

Input.propTypes = {
  type: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  extraClasses: PropTypes.string,
  labelExtraClasses: PropTypes.string,
  labelText: PropTypes.string.isRequired,
  required: PropTypes.bool,
  value: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  isBlur: PropTypes.func.isRequired,
  touched: PropTypes.bool,
}

Input.defaultProps = {
  extraClasses: '',
  labelExtraClasses: '',
  required: false,
  touched: false,
}

export default Input
