import React, { useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

// Styles
import styles from './textarea.module.scss'

const Textarea = ({
  isBlur,
  touched,
  errors,
  rows,
  name,
  extraClasses,
  labelExtraClasses,
  labelText,
  required,
  value,
  onChange,
}) => {
  const [blur, setBlur] = useState(false)

  const textareaClasses = classNames({
    [styles.textarea]: true,
    [styles.clicked]: blur,
    [styles.filled]: !!errors !== true && !!value,
    [styles.inputError]: !!errors && !!touched,
    [extraClasses]: !!extraClasses,
  })

  const labelClasses = classNames({
    [styles.label]: true,
    [styles.clicked]: blur,
    [styles.filled]: !!errors !== true && !!value,
    [styles.inputError]: !!errors && !!touched,
    [labelExtraClasses]: !!labelExtraClasses,
  })

  return (
    <div className={textareaClasses}>
      <label className={labelClasses} htmlFor={name}>
        {`${labelText} ${required ? '*' : ''}`}
      </label>
      <textarea
        className={styles.field}
        name={name}
        value={value}
        rows={rows}
        onChange={onChange}
        onBlur={isBlur}
        onFocus={() => setBlur(true)}
        onBlurCapture={() => setBlur(false)}
      />
      {touched && errors && <div className={styles.error}>{errors}</div>}
    </div>
  )
}

Textarea.propTypes = {
  rows: PropTypes.number,
  name: PropTypes.string.isRequired,
  extraClasses: PropTypes.string,
  labelText: PropTypes.string.isRequired,
  labelExtraClasses: PropTypes.string,
  required: PropTypes.bool,
  value: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
}

Textarea.defaultProps = {
  extraClasses: '',
  labelExtraClasses: '',
  required: false,
  rows: 3,
}

export default Textarea
