import { graphql, useStaticQuery } from 'gatsby'

const useNavigation = () => {
  const data = useStaticQuery(graphql`
    query {
      strapi {
        mainMenus {
          item {
            order
            page {
              slug
              title
            }
          }
        }
      }
    }
  `)

  return data.strapi.mainMenus.map(items => {
    return items
  })
}

export default useNavigation
