import React, { useEffect, useState } from 'react'
import { Link } from 'gatsby'
import classNames from 'classnames'

// Components
import Nav from './Nav'
import MetaNav from './MetaNav/MetaNav'

// Styles
import styles from './header.module.scss'

// Icons
import LogoIcon from './logo.inline.svg'
import MenuIcon from './menu-icon.inline.svg'
import ClosIcon from './close.inline.svg'

// Hooks
// import useNavigation from '../../hooks/useNavigation'

const Header = () => {
  // const link = useNavigation()
  const [isVisible, setIsVisible] = useState(false)
  const [scrollY, setScrollY] = useState(0)

  useEffect(() => {
    window.addEventListener('scroll', () => setScrollY(window.pageYOffset))

    return () => {
      window.removeEventListener('scroll', () => setScrollY(0))
    }
  }, [])

  const burgerClasses = classNames({
    [styles.burger]: true,
    [styles.hide]: isVisible,
    [styles.show]: !isVisible,
  })

  const closeClasses = classNames({
    [styles.close]: true,
    [styles.show]: isVisible,
    [styles.hide]: !isVisible,
  })

  const headerClasses = classNames({
    [styles.header]: true,
    [styles.scrolled]: scrollY > 120,
  })

  return (
    <header className={headerClasses}>
      <Link to="/" className={styles.logoContainer}>
        <LogoIcon className={styles.logo} />
      </Link>
      <button
        onClick={() => setIsVisible(!isVisible)}
        className={styles.burgerButton}
      >
        <MenuIcon className={burgerClasses} />
        <ClosIcon className={closeClasses} />
      </button>
      <Nav
        scrolled={scrollY > 120}
        visible={isVisible}
        setMenuVisible={setIsVisible}
      />
      {/* <MetaNav scrolled={scrollY > 120} /> */}
    </header>
  )
}

export default Header
