import React from 'react'
import { Link } from 'gatsby'
import classNames from 'classnames'

// Styles
import styles from './nav.module.scss'
import useNavigation from '../../hooks/useNavigation'

const Nav = ({ visible, setMenuVisible, ...props }) => {
  // Get the menu Items from our query
  const menuItems = useNavigation()

  let items = []

  // Clean up the menu items
  menuItems.forEach(menuItem => items.push(menuItem.item))

  // Sort the menu items based on the order given from the back-end
  items.sort((el1, el2) => {
    return el1.order > el2.order ? 1 : -1
  })

  const navClasses = classNames({
    [styles.nav]: true,
    [styles.visible]: visible,
  })

  const navWrapperClasses = classNames({
    [styles.navWrapper]: true,
  })

  return (
    <nav className={navClasses}>
      <div className={navWrapperClasses}>
        <a
          href="https://shop.safro-drink.com/products/safro"
          className={styles.menuLink}
        >
          Shop
        </a>
        {items &&
          items.map((item, key) => {
            return (
              <Link
                key={key}
                className={styles.menuLink}
                activeClassName={styles.active}
                to={`/${item.page.slug}`}
                onClick={() => setMenuVisible()}
              >
                {item.page.title}
              </Link>
            )
          })}
      </div>
    </nav>
  )
}

export default Nav
